idth:49.5%; \n    float:left;\n    border-right: 1px dotted #CCC;\n    box-sizing: border-box;\n    padding: 0px 15px 0px 0px;\n    overflow: hidden;\n    clear: both;\n  }\n  \n #companyinfo {\n    width:49.5%;\n    float:right;\n    box-sizing: border-box;\n    padding: 0px 0px 0px 15px;\n    overflow: hidden;\n    clear: both;\n}\n\n#register-form-footer{\n    border-top: 2px #917e9e;\n}\n\n\n.fixed-horizontal-center {\n    display: block;\n    margin-left: auto;\n    margin-right: auto;\n    width: 50%;  \n}\n\n.error {\n    color: red;\n}\n\n\np {\n    text-align: left;\n    color: white;\n    /* font-size: 15px */\n    font: 20px \"roboto\";\n}\n\n.btn#btn-login {\n    color: white;\n    border-color: #361555;\n    background-color: #361555;\n    justify-content: center;\n    display: inline-block;\n    border-radius: 5px;\n}\n\n\n\n/* Responsive ish */\n\n.responsive-component.small body.register{\n    padding-left: 10px;\n    padding-right: 10px;\n}\n\n.responsive-component.small .fixed-horizontal-center {\n    display: block;\n    margin-left: auto;\n    margin-right: auto;\n    width: 100%;  \n}\n\n/* ------Login Form--------- */\n\n.login {\n    padding-top: 100px;\n}\n\n#login {\n    position: fixed;\n    margin-bottom:10px;\n    top: 25px;\n    left: 50%;\n    width: 350px;\n    -webkit-transform: translateX(-50%);\n    -moz-transform: translateX(-50%);\n    -ms-transform: translateX(-50%);\n    -o-transform: translateX(-50%);\n    transform: translateX(-50%);\n}\n\n#login h1 {\n    text-align: center;\n    text-transform: uppercase;\n}\n\n\n\n.login #login label{\n    font: 18px 'Roboto';\n    color: rgb(94, 94, 94);\n    text-align:left;\n}\n\n.login #login h1 {\n    font: 28px 'Roboto';\n    color: #29105b;\n}\np {\n    font-size: 10px;\n    text-align: left;\n}\n\n/* button */\n\n\n#legal-links {\n    letter-spacing: 1px;\n    font: 15px 'Roboto';\n    text-align: center;\n    /* color: #917e9e; */\n    color: #29105b;\n}\n\n\n\n#login-logo {\n    display: block;\n    margin-left: auto;\n    margin-right: auto;\n    width: 175px;\n    height:150px;\n}\n\n/* loading animation - replace filled form with loading animation*/\n.loader {\n    color: #3c1760;\n    font-size: 20px;\n    margin: 100px auto;\n    width: 1em;\n    height: 1em;\n    border-radius: 50%;\n    position: relative;\n    text-indent: -9999em;\n    -webkit-animation: load4 1.3s infinite linear;\n    animation: load4 1.3s infinite linear;\n    -webkit-transform: translateZ(0);\n    -ms-transform: translateZ(0);\n    transform: translateZ(0);\n}\n\n@-webkit-keyframes load4 {\n0%,\n100% {\n    box-shadow: 0 -3em 0 0.2em, 2em -2em 0 0em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 0;\n}\n12.5% {\n    box-shadow: 0 -3em 0 0, 2em -2em 0 0.2em, 3em 0 0 0, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;\n}\n25% {\n    box-shadow: 0 -3em 0 -0.5em, 2em -2em 0 0, 3em 0 0 0.2em, 2em 2em 0 0, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;\n}\n37.5% {\n    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 0, 2em 2em 0 0.2em, 0 3em 0 0em, -2em 2em 0 -1em, -3em 0em 0 -1em, -2em -2em 0 -1em;\n}\n50% {\n    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 0em, 0 3em 0 0.2em, -2em 2em 0 0, -3em 0em 0 -1em, -2em -2em 0 -1em;\n}\n62.5% {\n    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 0, -2em 2em 0 0.2em, -3em 0 0 0, -2em -2em 0 -1em;\n}\n75% {\n    box-shadow: 0em -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0.2em, -2em -2em 0 0;\n}\n87.5% {\n    box-shadow: 0em -3em 0 0, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0, -2em -2em 0 0.2em;\n}\n}\n@keyframes load4 {\n0%,\n100% {\n    box-shadow: 0 -3em 0 0.2em, 2em -2em 0 0em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 0;\n}\n12.5% {\n    box-shadow: 0 -3em 0 0, 2em -2em 0 0.2em, 3em 0 0 0, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;\n}\n25% {\n    box-shadow: 0 -3em 0 -0.5em, 2em -2em 0 0, 3em 0 0 0.2em, 2em 2em 0 0, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;\n}\n37.5% {\n    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 0, 2em 2em 0 0.2em, 0 3em 0 0em, -2em 2em 0 -1em, -3em 0em 0 -1em, -2em -2em 0 -1em;\n}\n50% {\n    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 0em, 0 3em 0 0.2em, -2em 2em 0 0, -3em 0em 0 -1em, -2em -2em 0 -1em;\n}\n62.5% {\n    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 0, -2em 2em 0 0.2em, -3em 0 0 0, -2em -2em 0 -1em;\n}\n75% {\n    box-shadow: 0em -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0.2em, -2em -2em 0 0;\n}\n87.5% {\n    box-shadow: 0em -3em 0 0, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0, -2em -2em 0 0.2em;\n}\n}\n\n/* Buyer Login */\n\n.login #buyer-login h1 {\n    font: 28px 'Roboto';\n    color: #29105b;\n}\n","<template>\n<div>\n  <buyer-header></buyer-header>\n  <div\n    class=\"responsive-component\"\n    v-responsive=\"{\n      small: el => el.width < 430,\n      large: el=> el.width > 1030\n      }\"\n  >\n    <body class=\"register\">\n      <div class=\"fixed-horizontal-center\">\n        <!-- Loadng animation -->\n        <!-- <div class=\"loader\">Loading...</div> -->\n        <!-- Form -->\n        <b-card bg-variant=\"light\" style=\"max-width: 100rem;\" id=\"register\">\n          <h1>Buyer Sign Up</h1>\n          <br>\n          <!-- <hr> -->\n          <br>\n          <div class=\"error\" v-html=\"error\"/>\n          <!-- input for name -->\n          <form>\n            <div>Personal Info</div>\n            <hr>\n            <div class=\"form-group row\">\n              <label for=\"name\" class=\"col-sm col-form-label\">Name:</label>\n              <div class=\"col-sm-12\">\n                <input\n                  v-model=\"name\"\n                  type=\"text\"\n                  class=\"form-control\"\n                  id=\"staticEmail\"\n                  value=\"Enter your name\"\n                >\n              </div>\n            </div>\n\n            <div class=\"form-group row\">\n              <label for=\"email\" class=\"col-sm col-form-label\">Email:</label>\n              <div class=\"col-sm-12\">\n                <input\n                  v-model=\"email\"\n                  type=\"email\"\n                  class=\"form-control\"\n                  placeholder=\"Enter your email\"\n                >\n              </div>\n            </div>\n\n            <div class=\"form-group row\">\n              <label for=\"inputPassword\" class=\"col-sm col-form-label\">Password:</label>\n              <div class=\"col-sm-12\">\n                <input\n                  v-model=\"password\"\n                  type=\"password\"\n                  class=\"form-control\"\n                  placeholder=\"Enter your password\"\n                >\n              </div>\n            </div>\n\n            <div class=\"form-group row\">\n              <label for=\"inputPassword\" class=\"col-sm col-form-label\">Confirm Password:</label>\n              <div class=\"col-sm-12\">\n                <input\n                  v-model=\"passwordConfirm\"\n                  type=\"password\"\n                  class=\"form-control\"\n                  placeholder=\"Re-enter your password\"\n                >\n              </div>\n            </div>\n\n            <div class=\"form-group row\">\n              <label for=\"inputPhoneNumber\" class=\"col-sm col-form-label\">Phone Number:</label>\n              <div class=\"col-sm-12\">\n                <input\n                  v-model=\"number\"\n                  type=\"number\"\n                  class=\"form-control\"\n                  placeholder=\"Enter your phone number\"\n                >\n              </div>\n            </div>\n          </form>\n\n          <button type=\"button\" class=\"btn btn-outline pull-right\" id=\"submit\" @click=\"register\">\n            <h3>Submit</h3>\n          </button>\n        </b-card>\n        <br>\n        <br>\n        <!-- Footer -->\n        <div id=\"register-form-footer\">\n          <hr>\n          <b-container id=\"legal-links\">\n            <b-row>\n              <b-col cols=\"3\"></b-col>\n              <b-col cols=\"4\">\n                <h5>Terms and Conditions</h5>\n              </b-col>\n              <b-col cols=\"2\">\n                <h5>Privacy</h5>\n              </b-col>\n            </b-row>\n          </b-container>\n          <br>\n          <p style=\"text-align:center\">Fig Analytics is a certified C Corporation.</p>\n          <p style=\"text-align:center\">&copy; FIG 2018. All Rights Reserved.</p>\n        </div>\n      </div>\n    </body>\n  </div>\n</div>\n</template>\n\n<script>\nimport PageHeader from \"@/components/Header.vue\";\nimport BuyerHeader from \"@/components/buyerComponents/BuyerHeader.vue\";\nimport AuthenticationService from \"@/services/AuthenticationService\";\nimport Api from \"@/services/Api\";\nimport { ResponsiveDirective } from \"vue-responsive-components\";\n\nexport default {\n  data() {\n    return {\n      name: \"\",\n      email: \"\",\n      password: \"\",\n      passwordConfirm: \"\",\n      number: \"\",\n      contactListTableId: \"\",\n      error: null\n    };\n  },\n  components: {\n    BuyerHeader\n  },\n  directives: {\n    responsive: ResponsiveDirective\n  },\n  methods: {\n    // Prefills registration\n    // info if it was provided on the homepage\n    async register() {\n      try {\n        const response = await AuthenticationService.buyerRegister({\n          name: this.name,\n          email: this.email,\n          password: this.password,\n          passwordConfirm: this.passwordConfirm,\n          number: this.number\n        });\n        localStorage.setItem(\"id_token\", response.data.token);\n        this.$store.dispatch(\"setToken\", response.data.token);\n        this.$store.dispatch(\"setBuyer\", response.data.buyer);\n        Api().defaults.headers.common[\n          \"Authorization\"\n        ] = AuthenticationService.getAuthHeader();\n\n        const shoppingCartFlag = this.$store.getters.getShoppingCartFlag;\n\n        if (shoppingCartFlag == true) {\n          console.log(`\\nRedirecting onto buyer checkout\\n`); // TESTING\n          this.$router.push({\n            name: \"buyerCheckout\"\n          });\n        } else {\n          console.log(`\\nRedirecting onto buyer dashboard\\n`); // TESTING\n          this.$router.push({\n            name: \"buyerDashboard\"\n          });\n        }\n      } catch (error) {\n        console.log(`\\nAn error occurred in register ${error}\\n`); // TESTING\n        this.error = error.response.data.error;\n        if (error) throw error;\n      }\n    }\n  }\n};\n</script>\n\n<style scoped>\n@import \"../../assets/css/forms.css\";\n.error {\n  color: red;\n}\n</style>\n"],"sourceRoot":""}]);

