;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;AAEJ;;IAEI;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;AAEJ,UAAU;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;AAEV;;;;;;;;;;;;;;;;;;;;;;;;;IAyBI","file":"Dashboard.vue","sourcesContent":["body.dashboard {\n  /* opacity: 0.50; */\n  text-align: justify;\n  background-position: center;\n  background-repeat: no-repeat;\n  background-size: cover;\n  min-height: 100vh;\n  padding: 100px 80px;\n  /* background: linear-gradient(to bottom right, #ffffff 0%, #d8d4dc 100%); */\n  background: linear-gradient(to bottom right, #ffffff 0%, #c7c4d1 100%);\n}\n\n#dashboard #name-location {\n  max-width: 1314px;\n}\n\n\n#dashboard .services {\n  /* background-color: rgb(246, 244, 248); */\n  /* background-color: #311c63; */\n  background-color: #f8f8f8;\n  border-width: 1px;\n  border-color: rgba(34, 34, 34, 0.1);\n  border-style: solid;\n}\n\n#dashboard #stats {\n  background-color: white;\n  padding: 10px;\n}\n\n#stats .card {\n  border-color: rgb(190, 187, 187);\n  border-width: 1px; \n  border-style: solid;\n}\n\n#services {\n  padding-left: 20px;\n}\n\n.dashboard #about {\n  display: inline-block;\n  padding-left: 50px;\n  padding-top: 10px;\n  background-color: white;\n  border-width: .5px;\n  border-color: rgb(190, 187, 187);\n  border-style: solid;\n  max-width: 1314px;\n}\n\n.dashboard #name-location {\n  background: linear-gradient( white 60%, #311c63 40%);\n  border-color: rgb(190, 1