AiB;EACjB,qBAAqB;CACtB;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;AAED,6CAA6C;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;AAC7C,6EAA6E;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;AAE7E,sBAAsB;AAEtB;EACE,mBAAmB;EACnB,kCAAkC;EAClC,oBAAoB;EACpB,gBAAgB;EAChB,0BAA0B;EAC1B,mBAAmB;EACnB,eAAe;EACf,yBAAwB;MAAxB,sBAAwB;UAAxB,wBAAwB;CACzB;AAED;EACE,mBAAmB;EACnB,kCAAkC;EAClC,oBAAoB;EACpB,gBAAgB;EAChB,0BAA0B;EAC1B,mBAAmB;EACnB,eAAe;EACf,yBAAwB;MAAxB,sBAAwB;UAAxB,wBAAwB;EACxB,0BAA0B;CAC3B;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;AAED,uBAAuB;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;AACvB;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;IAgDI;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;AAKJ,yBAAyB;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;AAKzB,mGAAmG;AAEnG;EACE,oBAAoB;EACpB,mBAAmB;EACnB,oBAAoB;EACpB,4BAA4B;EAC5B,6BAA6B;EAC7B,uBAAuB;EACvB,kBAAkB;EAClB,kBAAkB;EAClB,gDAAgD;EAChD,yFAAuE;EAAvE,uEAAuE;CACxE;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;AAGD;;;;;KAKK;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;AAIL;;;;;;;;;;IAUI;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;AAIJ,UAAU;AACV;EACE,wBAAwB;EACxB,aAAa;EACb,cAAc;EAGd,oBAAoB;EACpB,6BAA6B;EAC7B,uBAAuB;EACvB,gDAAsD;CACvD;AAGD;EACE,0BAA0B;EAC1B,aAAa;EACb,cAAc;EAGd,oBAAoB;EACpB,6BAA6B;EAC7B,uBAAuB;EACvB,cAAc;CACf;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;AAED;;;IAGI;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;AAEJ;;IAEI;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;AAEJ,UAAU;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;AAEV;;;;;;;;;;;;;;;;;;;;;;;;;IAyBI;ACnMJ;EACA,kBAAA;EACA,mBAAA;CACA;AACA;EACA,oBAAA;EACA,wBAAA;CACA;AACA;EACA,oBAAA;EACA,wBAAA;CACA","file":"PublicProfile.vue","sourcesContent":["body.dashboard {\n  /* opacity: 0.50; */\n  text-align: justify;\n  background-position: center;\n  background-repeat: no-repeat;\n  background-size: cover;\n  min-height: 100vh;\n  padding: 100px 80px;\n  /* background: linear-gradient(to bottom right, #ffffff 0%, #d8d4dc 100%); */\n  background: linear-gradient(to bottom right, #ffffff 0%, #c7c4d1 100%);\n}\n\n#dashboard #name-location {\n  max-width: 1314px;\n}\n\n\n#dashboard .services {\n  /* background-color: rgb(246, 244, 248); */\n  /* background-color: #311c63; */\n  background-color: #f8f8f8;\n  border-width: 1px;\n  border-color: rgba(34, 34, 34, 0.1);\n  border-style: solid;\n}\n\n#dashboard #stats {\n  background-color: white;\n  padding: 10px;\n}\n\n#stats .card {\n  border-color: rgb(190, 187, 187);\n  border-width: 1px; \n  border-style: solid;\n}\n\n#services {\n  padding-left: 20px;\n}\n\n.dashboard #about {\n  display: inline-block;\n  padding-left: 50px;\n  padding-top: 10px;\n  background-color: white;\n  border-width: .5px;\n  border-color: rgb(190, 187, 187);\n  border-style: solid;\n  max-width: 1314px;\n}\n\n.dashboard #name-location {\n  background: linear-gradient( white 60%, #311c63 40%);\n  border-color: rgb(190, 187, 187);\n  border-style: solid;\n  border-width: .5px;\n  padding-top: 13px;\n  padding-bottom:4px;\n}\n\n\n/* Profile photo - company logo */\n.circle,\n#logo {\n  background-color: white;\n  width: 150px;\n  height: 150px;\n  -moz-border-radius: 75px;\n  -webkit-border-radius: 75px;\n  border-radius: 75px;\n  background-repeat: no-repeat;\n  background-size: cover;\n  background-image: url(\"./Icons/noun_DNA_2362091.png\");\n}\n\n#dashboard #logo-border {\n  background-color: #22104b;\n  width: 170px;\n  height: 170px;\n  -moz-border-radius: 85px;\n  -webkit-border-radius: 85px;\n  border-radius: 85px;\n  background-repeat: no-repeat;\n  background-size: cover;\n  padding: 10px;\n}\n\n#price-chart {\n  border-color: rgb(190, 187, 187);\n  border-style: solid;\n  border-width: .5px;\n  max-width: 800px;\n}\n\n/*------------- Text ------------------------*/\n.dashboard #dashboard #add-service p {\n  font-size: 15px;\n  text-align: left;\n}\n\n.dashboard #dashboard h1 {\n  letter-spacing: 1px;\n  /* text-transform: uppercase; */\n  font-style: medium;\n  font: 35px \"Roboto\";\n  color: #311c63;\n  text-align: center;\n}\n\n.dashboard #dashboard h3 {\n  letter-spacing: 1px;\n  /* text-transform: uppercase; */\n  font-style: medium;\n  font: 25px \"Roboto\";\n  color: #311c63;\n  text-align: center;\n}\n\n.services h4 {\n  letter-spacing: 1px;\n  /* font-style: medium; */\n  font: 20px \"Roboto\";\n  color: #311c63;\n  text-align: left;\n}\n\n.dashboard #dashboard h4 {\n  letter-spacing: 1px;\n  /* font-style: medium; */\n  font: 20px \"Roboto\";\n  color: #311c63;\n\n}\n\n#dashboard #about h4 {\n  /* font-style: medium; */\n  font: 19px \"Roboto\";\n  color: black;\n  text-align: center;\n}\n\n#dashboard #about p {\n  letter-spacing: 1px;\n  /* font-style: medium; */\n  font: 17px \"Roboto\";\n  color: black;\n  text-align: center;\n\n}\n\n.dashboard #dashboard #stats h4 {\n  letter-spacing: 1px;\n  /* font-style: medium; */\n  font: 18px \"Roboto\";\n  color: black;\n  text-align: center;\n}\n\n/* #dashboard #services h5 {\n  font: 12px \"Roboto\";\n  color: #111;\n} */\n\n#dashboard #services h6 {\n  font: 17px \"Roboto\";\n  color: white;\n}\n\n#dashboard #stats h6 {\n  font: 15px \"Roboto\";\n  color: #111;\n  text-align: center;\n}\n\n.card-text {\n  font: 18px \"Roboto\";\n  color: #111;\n  text-align: center;\n}\n\n#dashboard p {\n  font: 20px \"Roboto\";\n  color: #111;\n  text-align: center;\n}\n\n#dashboard #price-chart p  {\n  font: 16px \"Roboto\";\n  color: #111;\n}\n\n#dashboard #spec_inquiry p {\n  text-align: left;\n}\n\n/* Responsive Text */\n\na {\n  color: #311c63;\n  -webkit-transition: all 0.35s;\n  transition: all 0.35s;\n}\n\na:hover,\na:focus {\n  color: #6a2c86;\n}\n\nhr {\n  max-width: 100px;\n  margin: 25px auto 0;\n  border-width: 1px;\n  border-color: rgba(34, 34, 34, 0.1);\n}\n\np {\n  font-size: 18px;\n  line-height: 1.5;\n  /* color: #dad8d8; */\n  margin-bottom: 20px;\n}\n\n.form p {\n  font-size: 12px;\n  line-height: 1.5;\n  /* color: #dad8d8; */\n}\n\n/* ------------- buttons ----------------- */\n/* The following button css was copied directly from FigAnal for reference */\n\n/* Stats View Leads */\n\n.btn-quote-req{\n  border-radius: 0px;\n  font-family: 'Roboto', sans-serif;\n  letter-spacing: 2px;\n  font-size: 12px;\n  text-transform: uppercase;\n  font-style: medium;\n  margin: 0 auto;\n  justify-content: center;\n}\n\n.btn-gen-inq {\n  border-radius: 0px;\n  font-family: 'Roboto', sans-serif;\n  letter-spacing: 2px;\n  font-size: 12px;\n  text-transform: uppercase;\n  font-style: medium;\n  margin: 0 auto;\n  justify-content: center;\n  background-color: #2238ff;\n}\n\n/* Stats Add credits */\n/* .btn-add-service {\n  border-radius: 300px;\n  font-family: 'Roboto', sans-serif;\n  letter-spacing: 2px;\n  font-size: 12px;\n  text-transform: uppercase;\n  font-style: medium;\n  margin: 0 auto;\n  justify-content: center;\n}\n\n.btn-login {\n  border-radius: 300px;\n  font-family: 'Roboto', sans-serif;\n  letter-spacing: 2px;\n  text-transform: uppercase;\n  font-style: medium;\n  margin: 0 auto;\n  justify-content: center;\n}\n\n.btn-signup-outline {\n  color: rgb(190, 190, 190);\n  border: 1px solid;\n  border-color: rgb(190, 190, 190);\n}\n\n.btn-login-outline {\n  color: rgb(190, 190, 190);\n  border: 1px solid;\n  border-color: rgb(190, 190, 190);\n}\n\n.btn-signup-outline:hover, .btn-signup-outline:focus, .btn-signup-outline.active {\n  color: white;\n  border-color: #361555;\n  background-color: #361555;\n}\n\n.btn-login-outline:hover, .btn-login-outline:focus, .btn-login-outline.active {\n  color: white;\n  border-color: #088a12;\n  background-color: #088a12;\n}\n\n.btn-xl {\nfont-size: 11px;\npadding: 15px 45px;\n} */\n\n\n\n\n/* Tagging Basic Style */\n\n\n\n\n/* ------Vue Directive Responsive Design Ish ----------------------------------------------------*/\n\n.responsive-component.small body.dashboard {\n  /* opacity: 0.50; */\n  text-align: center;\n  text-align: justify;\n  background-position: center;\n  background-repeat: no-repeat;\n  background-size: cover;\n  min-height: 100vh;\n  padding: 80px 0px;\n  /* background-image: url(\"./BACKGROUND.png\"); */\n  background: linear-gradient(to bottom right, #ffffff 0%, #d8d4dc 100%);\n} \n\n\n/* .responsive-component.small #name-location {\n  background: linear-gradient( white 70%, #311c63 30%);\n  border-color: rgb(190, 187, 187);\n  border-style: solid;\n  border-width: .5px;\n}  */\n\n\n\n/* .responsive-component #dashboard #company-name {\n\n  color: white;\n\n}\n\n.responsive-component #dashboard #company-location{\n  color: white;\n\n\n} */\n\n\n\n/* logo */\n.responsive-component.small #dashboard #logo {\n  background-color: white;\n  width: 100px;\n  height: 100px;\n  -moz-border-radius: 50px;\n  -webkit-border-radius: 50px;\n  border-radius: 50px;\n  background-repeat: no-repeat;\n  background-size: cover;\n  background-image: url(\"./Icons/noun_DNA_2362091.png\");\n}\n\n\n.responsive-component.small #dashboard #logo-border {\n  background-color: #22104b;\n  width: 120px;\n  height: 120px;\n  -moz-border-radius: 60px;\n  -webkit-border-radius: 60px;\n  border-radius: 60px;\n  background-repeat: no-repeat;\n  background-size: cover;\n  padding: 10px;\n}\n\n/* .responsive-component.small #company-name {\n\n  color: white;\n} */\n\n/* .responsive-component.small #dashboard #company-location{\n  padding-top: 20px;\n} */\n\n/* text */\n\n/* .responsive-component.small h1, h2 {\n  letter-spacing: 1px;\n  font-size: 28px;\n  text-align:center;\n}\n\n.responsive-component.small h3 {\n  letter-spacing: 1px;\n  font-size: 25px;\n  text-align:center;\n}\n\n.responsive-component.small p{\n  width:100%;\n  font-size: 20px;\n}\n\n.responsive-component.small footer p{\n  width:100%;\n  font-size: 10px;\n}\n\n.responsive-component.small form{\n  width:100%;\n  font-size: 12px;\n} */\n","<template>\n<div id=\"app\">\n  <buyer-header></buyer-header>\n  <div class=\"responsive-component\" v-responsive=\"{\n      small: el => el.width < 860\n      }\">\n    <body class=\"dashboard\">\n      <div class=\"container\" id=\"dashboard\">\n        <div id=\"main\">\n            <div class=\"container\" id=\"name-location\">\n\n        \n                <div class=\"d-flex justify-content-center\" id=\"company-name\">\n                    <h1 style=\"text-align:center:\">{{ this.companyName }}</h1>\n                </div>\n                    <div class=\"d-flex justify-content-center\" id=\"company-location\">\n                    <h4 style=\"text-align:center:\">Location: {{ this.companyLocation }}</h4>\n                </div>\n                    <div class=\"d-flex justify-content-center\" id=\"company-logo\">\n                    <div id= \"logo-border\">\n                        <div class=\"circle\" id=\"logo\"></div>\n                    </div>\n                </div>\n                                                      \n            </div>\n\n            <!-- About Section -->\n            <div class=\"container\" id=\"about\">                \n                <div class=\"row\">\n                    <h4>About:</h4><p style=\"text-align:left\"> {{ this.about }}</p>\n                </div>\n            </div>\n            \n        </div>\n      \n        <br>\n        <br>\n        <br>\n      \n\n      \n\n        <div class=\"services\">\n          <br>\n          <div id=\"services\">\n            <button @click=\"manifestModalInquiry(service)\" class=\"btn-gen-inq btn btn-primary pull-right\">General inquiry</button>\n            <h4> Services:</h4>\n              \n          </div>\n\n          <div class=\"col-12\"> \n\n            <div\n              class=\"card\"\n              v-for=\"(service, index) in this.services\"\n              :key=\"service.id\"\n            >\n              <h4 class=\"card-header\" style=\"text-align:left; text-indent:15px;\">{{ service.title }}</h4>\n              <div class=\"card-body\">\n                <!-- Service Description -->\n                <button @click=\"manifestModalForm(service)\" class=\"btn-quote-req btn btn-primary pull-right\">Request Quote</button>\n                <h5 class=\"card-text\" style=\"text-align:left; padding:15px;\">{{ service.description }}</h5>\n                \n                <!-- Service/Price Listings -->\n                \n                <div class=\"container\" id=\"price-chart\">\n                  <p>\n                    <table class=\"table table-hover\" >\n                      <thead>\n                          <tr>\n                              <th scope=\"col\">Service Subtitle</th>\n                              <th scope=\"col\">Turn Around Time</th>\n                              <th scope=\"col\">Price/ Unit</th>\n                          </tr>\n                      </thead>\n                      <tbody>\n                          <tr>\n                              <th scope=\"row\">Service1</th>\n                              <td>TT1</td>\n                              <td>P1</td>\n                              <td><button class=\"btn btn-success\" @click=\"addServiceToCart(service, index)\">Add to Cart</button></td>\n                          </tr>\n                          <tr>\n                              <th scope=\"row\">Service2</th>\n                              <td>TT2</td>\n                              <td>P2</td>\n                              <td><button class=\"btn btn-success\" @click=\"addServiceToCart(service, index)\">Add to Cart</button></td>\n                          </tr>\n\n                      </tbody>\n\n                    </table>\n                  </p>